'use client'

import { useState, useEffect } from 'react'
import Link from 'next/link'
import { Search, ShoppingCart, Menu, X, LogOut, User } from 'lucide-react'
import { useCart } from '@/context/cart-context'
import { useAuth } from '@/context/auth-context'
import { CartDrawer } from '@/components/cart-drawer'
import { ThemeToggle } from '@/components/theme-toggle'

export function Header() {
  const [isScrolled, setIsScrolled] = useState(false)
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false)
  const [isCartOpen, setIsCartOpen] = useState(false)
  const { items } = useCart()
  const { user, logout } = useAuth()

  useEffect(() => {
    const handleScroll = () => {
      setIsScrolled(window.scrollY > 50)
    }

    window.addEventListener('scroll', handleScroll)
    return () => window.removeEventListener('scroll', handleScroll)
  }, [])

  return (
    <>
      <header
        className={`sticky top-0 z-40 w-full smooth-transition ${
          isScrolled
            ? 'bg-background/95 backdrop-blur-sm border-b border-border shadow-sm'
            : 'bg-background'
        }`}
      >
        <div className="max-w-7xl mx-auto px-4 md:px-6 py-4">
          <div className="flex items-center justify-between gap-4">
            {/* Logo */}
            <Link
              href="/"
              className="text-2xl font-bold tracking-tight text-foreground hover:text-primary smooth-hover"
              style={{ fontFamily: 'cursive' }}
            >
              Fashino
            </Link>

            {/* Desktop Navigation */}
            <nav className="hidden md:flex items-center gap-8">
              <Link
                href="/shop"
                className="text-sm font-medium text-foreground hover:text-primary smooth-hover"
              >
                Shop
              </Link>
              <Link
                href="/men"
                className="text-sm font-medium text-foreground hover:text-primary smooth-hover"
              >
                Men
              </Link>
              <Link
                href="/women"
                className="text-sm font-medium text-foreground hover:text-primary smooth-hover"
              >
                Women
              </Link>
              <Link
                href="/shop"
                className="text-sm font-medium text-foreground hover:text-primary smooth-hover"
              >
                Collections
              </Link>
            </nav>

            {/* Right Section */}
            <div className="flex items-center gap-4">
              {/* Search Bar */}
              <div className="hidden sm:flex items-center bg-muted rounded-full pl-4 pr-2 py-2 smooth-transition hover:bg-secondary/20">
                <input
                  type="text"
                  placeholder="Search collections..."
                  className="bg-transparent outline-none text-sm w-32 placeholder:text-muted-foreground"
                />
                <Search className="w-4 h-4 text-muted-foreground cursor-pointer" />
              </div>

              {/* Theme Toggle */}
              <ThemeToggle />

              {/* User Account or Login */}
              {user ? (
                <div className="flex items-center gap-2">
                  <Link
                    href="/dashboard"
                    className="hidden sm:flex items-center gap-2 px-3 py-2 hover:bg-muted rounded-full smooth-hover"
                  >
                    {user.profilePic ? (
                      <img
                        src={user.profilePic || "/placeholder.svg"}
                        alt={user.name}
                        className="w-6 h-6 rounded-full object-cover"
                      />
                    ) : (
                      <User className="w-6 h-6 text-muted-foreground" />
                    )}
                    <span className="text-sm font-medium">{user.name || 'User'}</span>
                  </Link>
                  <button
                    onClick={logout}
                    className="p-2 hover:bg-muted rounded-full smooth-hover"
                    title="Logout"
                  >
                    <LogOut className="w-5 h-5 text-foreground" />
                  </button>
                </div>
              ) : (
                <Link
                  href="/login"
                  className="hidden sm:block px-4 py-2 text-sm font-medium bg-primary text-primary-foreground rounded-full hover:shadow-lg smooth-transition hover:scale-105"
                >
                  Sign In
                </Link>
              )}

              {/* Cart */}
              <button 
                onClick={() => setIsCartOpen(true)}
                className="relative p-2 hover:bg-muted rounded-full smooth-hover group"
              >
                <ShoppingCart className="w-5 h-5 text-foreground group-hover:text-primary smooth-transition" />
                {items.length > 0 && (
                  <span className="absolute top-1 right-1 w-5 h-5 bg-primary text-primary-foreground text-xs rounded-full flex items-center justify-center font-semibold animate-pulse">
                    {items.length}
                  </span>
                )}
              </button>

              {/* Mobile Menu Button */}
              <button
                className="md:hidden p-2 hover:bg-muted rounded-full smooth-hover"
                onClick={() => setIsMobileMenuOpen(!isMobileMenuOpen)}
              >
                {isMobileMenuOpen ? (
                  <X className="w-5 h-5" />
                ) : (
                  <Menu className="w-5 h-5" />
                )}
              </button>
            </div>
          </div>

          {/* Mobile Menu */}
          {isMobileMenuOpen && (
            <nav className="md:hidden mt-4 pb-4 space-y-3 fade-in">
              <Link href="/shop" className="block text-sm font-medium hover:text-primary smooth-hover">
                Shop
              </Link>
              <Link href="/men" className="block text-sm font-medium hover:text-primary smooth-hover">
                Men
              </Link>
              <Link href="/women" className="block text-sm font-medium hover:text-primary smooth-hover">
                Women
              </Link>
              <Link href="/shop" className="block text-sm font-medium hover:text-primary smooth-hover">
                Collections
              </Link>
              {!user && (
                <Link href="/login" className="block text-sm font-medium text-primary hover:underline">
                  Sign In
                </Link>
              )}
            </nav>
          )}
        </div>
      </header>

      {/* Cart Drawer */}
      <CartDrawer isOpen={isCartOpen} onClose={() => setIsCartOpen(false)} />
    </>
  )
}
