'use client'

import { useState, useEffect } from 'react'
import { Header } from '@/components/header'
import { Footer } from '@/components/footer'
import { useCart } from '@/context/cart-context'
import { ChevronRight, Lock, Check } from 'lucide-react'
import Link from 'next/link'
import { useSearchParams } from 'next/navigation'

type CheckoutStep = 'cart' | 'shipping' | 'payment' | 'confirmation'
type PaymentMethod = 'card' | 'upi' | 'cod' | 'netbanking'

export default function CheckoutPage() {
  const { items, total, clearCart } = useCart()
  const searchParams = useSearchParams()
  const startOnShipping = searchParams.get('start') === 'shipping' && items.length > 0
  const [currentStep, setCurrentStep] = useState<CheckoutStep>(startOnShipping ? 'shipping' : 'cart')
  const [paymentMethod, setPaymentMethod] = useState<PaymentMethod>('card')
  const [formData, setFormData] = useState({
    firstName: '',
    lastName: '',
    email: '',
    phone: '',
    address: '',
    city: '',
    state: '',
    zipCode: '',
    country: 'India',
    cardName: '',
    cardNumber: '',
    expiry: '',
    cvv: '',
    upiId: '',
  })

  useEffect(() => {
    if (searchParams.get('start') === 'shipping' && items.length > 0) {
      setCurrentStep('shipping')
    }
  }, [searchParams, items.length])

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLSelectElement>) => {
    const { name, value } = e.target
    setFormData((prev) => ({ ...prev, [name]: value }))
  }

  const handleContinue = () => {
    if (currentStep === 'cart') setCurrentStep('shipping')
    else if (currentStep === 'shipping') setCurrentStep('payment')
    else if (currentStep === 'payment') setCurrentStep('confirmation')
  }

  const handleBack = () => {
    if (currentStep === 'shipping') setCurrentStep('cart')
    else if (currentStep === 'payment') setCurrentStep('shipping')
  }

  const isShippingValid = formData.firstName && formData.lastName && formData.email && formData.address && formData.city && formData.zipCode
  const isPaymentValid = paymentMethod === 'card' 
    ? formData.cardName && formData.cardNumber && formData.expiry && formData.cvv
    : paymentMethod === 'upi'
    ? formData.upiId
    : true

  const shippingCost = total > 5000 ? 0 : 500
  const tax = Math.round(total * 0.18)
  const finalTotal = total + shippingCost + tax

  return (
    <>
      <Header />
      <main className="min-h-screen bg-background">
        {/* Progress Indicator */}
        {currentStep !== 'confirmation' && (
          <div className="bg-muted/50 px-4 md:px-6 py-8">
            <div className="max-w-7xl mx-auto">
              <div className="flex items-center justify-between">
                {(['cart', 'shipping', 'payment'] as const).map((step, index) => (
                  <div key={step} className="flex items-center flex-1">
                    <div
                      className={`flex items-center justify-center w-10 h-10 rounded-full font-bold smooth-transition ${
                        step === currentStep
                          ? 'bg-primary text-primary-foreground'
                          : currentStep === 'confirmation' || 
                            (['payment', 'confirmation'].includes(currentStep) && step === 'payment') ||
                            (['shipping', 'payment', 'confirmation'].includes(currentStep) && step === 'shipping')
                          ? 'bg-green-500 text-white'
                          : 'bg-border text-muted-foreground'
                      }`}
                    >
                      {step === 'confirmation' || 
                       (['payment', 'confirmation'].includes(currentStep) && step === 'payment') ||
                       (['shipping', 'payment', 'confirmation'].includes(currentStep) && step === 'shipping') ? (
                        <Check className="w-5 h-5" />
                      ) : (
                        index + 1
                      )}
                    </div>
                    <span className="text-xs font-semibold uppercase ml-2 text-foreground">{step}</span>
                    {index < 2 && (
                      <div className="flex-1 h-1 mx-4 bg-border smooth-transition"></div>
                    )}
                  </div>
                ))}
              </div>
            </div>
          </div>
        )}

        <div className="max-w-7xl mx-auto px-4 md:px-6 py-12">
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
            {/* Main Content */}
            <div className="lg:col-span-2">
              {/* CART STEP */}
              {currentStep === 'cart' && (
                <div className="space-y-6 fade-in">
                  <h2 className="text-2xl font-bold mb-6">Review Your Cart</h2>
                  
                  {items.length === 0 ? (
                    <div className="bg-muted/50 p-8 rounded-lg text-center">
                      <p className="text-muted-foreground mb-4">Your cart is empty</p>
                      <Link href="/">
                        <button className="px-6 py-2 bg-primary text-primary-foreground rounded-full hover:shadow-lg smooth-transition">
                          Continue Shopping
                        </button>
                      </Link>
                    </div>
                  ) : (
                    <div className="space-y-4">
                      {items.map((item) => (
                        <div
                          key={item.id}
                          className="flex gap-4 p-4 bg-muted/30 rounded-lg scale-in"
                        >
                          <img
                            src={item.image || '/placeholder.svg'}
                            alt={item.name}
                            className="w-24 h-24 object-cover rounded"
                          />
                          <div className="flex-1">
                            <h3 className="font-semibold">{item.name}</h3>
                            <p className="text-sm text-muted-foreground">Qty: {item.quantity}</p>
                            {item.size && (
                              <p className="text-sm text-muted-foreground">Size: {item.size}</p>
                            )}
                          </div>
                          <div className="text-right">
                            <p className="font-semibold">
                              â‚¹{(item.price * item.quantity).toLocaleString('en-IN')}
                            </p>
                          </div>
                        </div>
                      ))}
                    </div>
                  )}
                </div>
              )}

              {/* SHIPPING STEP */}
              {currentStep === 'shipping' && (
                <div className="space-y-6 fade-in">
                  <h2 className="text-2xl font-bold mb-6">Shipping Information</h2>
                  
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div>
                      <label className="block text-sm font-medium mb-2">First Name</label>
                      <input
                        type="text"
                        name="firstName"
                        value={formData.firstName}
                        onChange={handleInputChange}
                        className="w-full px-4 py-2 border border-border rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
                        placeholder="John"
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-medium mb-2">Last Name</label>
                      <input
                        type="text"
                        name="lastName"
                        value={formData.lastName}
                        onChange={handleInputChange}
                        className="w-full px-4 py-2 border border-border rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
                        placeholder="Doe"
                      />
                    </div>
                  </div>

                  <div>
                    <label className="block text-sm font-medium mb-2">Email</label>
                    <input
                      type="email"
                      name="email"
                      value={formData.email}
                      onChange={handleInputChange}
                      className="w-full px-4 py-2 border border-border rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
                      placeholder="john@example.com"
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium mb-2">Phone</label>
                    <input
                      type="tel"
                      name="phone"
                      value={formData.phone}
                      onChange={handleInputChange}
                      className="w-full px-4 py-2 border border-border rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
                      placeholder="+91 9876543210"
                    />
                  </div>

                  <div>
                    <label className="block text-sm font-medium mb-2">Address</label>
                    <input
                      type="text"
                      name="address"
                      value={formData.address}
                      onChange={handleInputChange}
                      className="w-full px-4 py-2 border border-border rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
                      placeholder="123 Main Street"
                    />
                  </div>

                  <div className="grid grid-cols-2 md:grid-cols-3 gap-4">
                    <div>
                      <label className="block text-sm font-medium mb-2">City</label>
                      <input
                        type="text"
                        name="city"
                        value={formData.city}
                        onChange={handleInputChange}
                        className="w-full px-4 py-2 border border-border rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
                        placeholder="New Delhi"
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-medium mb-2">State</label>
                      <input
                        type="text"
                        name="state"
                        value={formData.state}
                        onChange={handleInputChange}
                        className="w-full px-4 py-2 border border-border rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
                        placeholder="Delhi"
                      />
                    </div>
                    <div>
                      <label className="block text-sm font-medium mb-2">ZIP Code</label>
                      <input
                        type="text"
                        name="zipCode"
                        value={formData.zipCode}
                        onChange={handleInputChange}
                        className="w-full px-4 py-2 border border-border rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
                        placeholder="110001"
                      />
                    </div>
                  </div>
                </div>
              )}

              {/* PAYMENT STEP */}
              {currentStep === 'payment' && (
                <div className="space-y-6 fade-in">
                  <h2 className="text-2xl font-bold mb-6">Payment Information</h2>
                  
                  <div className="bg-primary/5 border border-primary/20 p-4 rounded-lg flex items-center gap-2 mb-6">
                    <Lock className="w-4 h-4 text-primary" />
                    <span className="text-sm text-foreground">Your payment is secure and encrypted</span>
                  </div>

                  <div className="space-y-4 mb-8 pb-8 border-b border-border">
                    <h3 className="font-semibold text-foreground">Select Payment Method</h3>
                    <div className="grid grid-cols-2 md:grid-cols-4 gap-3">
                      {[
                        { id: 'card', label: 'Card', icon: 'ðŸ’³' },
                        { id: 'upi', label: 'UPI', icon: 'ðŸ“±' },
                        { id: 'netbanking', label: 'Net Banking', icon: 'ðŸ¦' },
                        { id: 'cod', label: 'COD', icon: 'ðŸšš' },
                      ].map((method) => (
                        <button
                          key={method.id}
                          onClick={() => setPaymentMethod(method.id as PaymentMethod)}
                          className={`p-4 rounded-lg border-2 smooth-transition text-center font-medium ${
                            paymentMethod === method.id
                              ? 'border-primary bg-primary/10 text-primary'
                              : 'border-border text-foreground hover:border-primary/50'
                          }`}
                        >
                          <div className="text-2xl mb-2">{method.icon}</div>
                          {method.label}
                        </button>
                      ))}
                    </div>
                  </div>

                  {/* Card Payment Form */}
                  {paymentMethod === 'card' && (
                    <div className="space-y-4">
                      <div>
                        <label className="block text-sm font-medium mb-2">Cardholder Name</label>
                        <input
                          type="text"
                          name="cardName"
                          value={formData.cardName}
                          onChange={handleInputChange}
                          className="w-full px-4 py-2 border border-border rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
                          placeholder="John Doe"
                        />
                      </div>

                      <div>
                        <label className="block text-sm font-medium mb-2">Card Number</label>
                        <input
                          type="text"
                          name="cardNumber"
                          value={formData.cardNumber}
                          onChange={handleInputChange}
                          maxLength={16}
                          placeholder="1234 5678 9012 3456"
                          className="w-full px-4 py-2 border border-border rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
                        />
                      </div>

                      <div className="grid grid-cols-2 gap-4">
                        <div>
                          <label className="block text-sm font-medium mb-2">Expiry Date</label>
                          <input
                            type="text"
                            name="expiry"
                            value={formData.expiry}
                            onChange={handleInputChange}
                            maxLength={5}
                            placeholder="MM/YY"
                            className="w-full px-4 py-2 border border-border rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
                          />
                        </div>
                        <div>
                          <label className="block text-sm font-medium mb-2">CVV</label>
                          <input
                            type="text"
                            name="cvv"
                            value={formData.cvv}
                            onChange={handleInputChange}
                            maxLength={3}
                            placeholder="123"
                            className="w-full px-4 py-2 border border-border rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
                          />
                        </div>
                      </div>
                    </div>
                  )}

                  {/* UPI Payment Form */}
                  {paymentMethod === 'upi' && (
                    <div>
                      <label className="block text-sm font-medium mb-2">UPI ID</label>
                      <input
                        type="text"
                        name="upiId"
                        value={formData.upiId}
                        onChange={handleInputChange}
                        className="w-full px-4 py-2 border border-border rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
                        placeholder="yourname@upi"
                      />
                      <p className="text-xs text-muted-foreground mt-2">Enter your UPI ID (e.g., name@googleplay)</p>
                    </div>
                  )}

                  {/* Net Banking Payment Form */}
                  {paymentMethod === 'netbanking' && (
                    <div>
                      <label className="block text-sm font-medium mb-2">Select Your Bank</label>
                      <select
                        className="w-full px-4 py-2 border border-border rounded-lg focus:outline-none focus:ring-2 focus:ring-primary"
                      >
                        <option>Select a bank</option>
                        <option>HDFC Bank</option>
                        <option>ICICI Bank</option>
                        <option>State Bank of India</option>
                        <option>Axis Bank</option>
                        <option>Other Banks</option>
                      </select>
                    </div>
                  )}

                  {/* COD Payment Form */}
                  {paymentMethod === 'cod' && (
                    <div className="bg-blue-50 dark:bg-blue-900/20 p-4 rounded-lg border border-blue-200 dark:border-blue-800">
                      <p className="text-sm text-blue-900 dark:text-blue-200">
                        You will pay â‚¹{finalTotal.toLocaleString('en-IN')} on delivery. Your order will be delivered in 5-7 business days.
                      </p>
                    </div>
                  )}
                </div>
              )}

              {/* CONFIRMATION STEP */}
              {currentStep === 'confirmation' && (
                <div className="text-center space-y-6 fade-in py-12">
                  <div className="flex justify-center mb-6">
                    <div className="w-16 h-16 bg-green-500 rounded-full flex items-center justify-center animate-pulse">
                      <Check className="w-8 h-8 text-white" />
                    </div>
                  </div>
                  <h2 className="text-3xl font-bold">Order Confirmed!</h2>
                  <p className="text-lg text-muted-foreground max-w-md mx-auto">
                    Thank you for your purchase. Your order has been confirmed and you'll receive an email confirmation shortly.
                  </p>
                  <div className="bg-muted/50 p-6 rounded-lg text-left max-w-md mx-auto">
                    <p className="text-sm text-muted-foreground mb-2">Order Number</p>
                    <p className="text-2xl font-bold mb-6">#{Math.random().toString(36).substring(2, 9).toUpperCase()}</p>
                    <p className="text-sm text-muted-foreground mb-2">Estimated Delivery</p>
                    <p className="font-semibold">5-7 Business Days</p>
                  </div>
                  <div className="flex flex-col sm:flex-row gap-4 justify-center pt-6">
                    <Link href="/">
                      <button className="px-8 py-3 bg-primary text-primary-foreground rounded-full font-semibold hover:shadow-lg smooth-transition">
                        Continue Shopping
                      </button>
                    </Link>
                    <button className="px-8 py-3 border-2 border-primary text-primary rounded-full font-semibold hover:bg-primary/5 smooth-transition">
                      Track Order
                    </button>
                  </div>
                </div>
              )}

              {/* Navigation Buttons */}
              {currentStep !== 'confirmation' && (
                <div className="flex gap-4 mt-12">
                  {currentStep !== 'cart' && (
                    <button
                      onClick={handleBack}
                      className="px-8 py-3 border-2 border-primary text-primary rounded-full font-semibold hover:bg-primary/5 smooth-transition"
                    >
                      Back
                    </button>
                  )}
                  {currentStep === 'cart' && items.length > 0 && (
                    <button
                      onClick={handleContinue}
                      className="ml-auto px-8 py-3 bg-primary text-primary-foreground rounded-full font-semibold hover:shadow-lg smooth-transition active:scale-95 flex items-center gap-2"
                    >
                      Continue <ChevronRight className="w-4 h-4" />
                    </button>
                  )}
                  {currentStep === 'shipping' && (
                    <button
                      onClick={handleContinue}
                      disabled={!isShippingValid}
                      className="ml-auto px-8 py-3 bg-primary text-primary-foreground rounded-full font-semibold hover:shadow-lg smooth-transition active:scale-95 disabled:opacity-50 flex items-center gap-2"
                    >
                      Continue <ChevronRight className="w-4 h-4" />
                    </button>
                  )}
                  {currentStep === 'payment' && (
                    <button
                      onClick={handleContinue}
                      disabled={!isPaymentValid}
                      className="ml-auto px-8 py-3 bg-primary text-primary-foreground rounded-full font-semibold hover:shadow-lg smooth-transition active:scale-95 disabled:opacity-50 flex items-center gap-2"
                    >
                      Complete Order <ChevronRight className="w-4 h-4" />
                    </button>
                  )}
                </div>
              )}
            </div>

            {/* Order Summary */}
            <div className="lg:col-span-1">
              <div className="bg-muted/50 rounded-lg p-6 sticky top-24 scale-in">
                <h3 className="text-lg font-bold mb-4">Order Summary</h3>
                
                <div className="space-y-3 mb-6 pb-6 border-b border-border">
                  <div className="flex justify-between text-sm">
                    <span className="text-muted-foreground">Subtotal</span>
                    <span className="font-medium">â‚¹{total.toLocaleString('en-IN')}</span>
                  </div>
                  <div className="flex justify-between text-sm">
                    <span className="text-muted-foreground">Shipping</span>
                    <span className="font-medium">
                      {shippingCost === 0 ? 'Free' : `â‚¹${shippingCost.toLocaleString('en-IN')}`}
                    </span>
                  </div>
                  <div className="flex justify-between text-sm">
                    <span className="text-muted-foreground">Tax (18% GST)</span>
                    <span className="font-medium">â‚¹{tax.toLocaleString('en-IN')}</span>
                  </div>
                </div>

                <div className="flex justify-between text-lg font-bold mb-6">
                  <span>Total</span>
                  <span>â‚¹{finalTotal.toLocaleString('en-IN')}</span>
                </div>

                <div className="space-y-2 text-xs text-muted-foreground">
                  <p>âœ“ Free shipping on orders over â‚¹5000</p>
                  <p>âœ“ 30-day money-back guarantee</p>
                  <p>âœ“ Secure checkout with SSL encryption</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </main>
      <Footer />
    </>
  )
}
